@font-face {
  font-family: 'montserrat';
  src: url("../fonts/montserrat-bold-webfont.woff2") format("woff2"), url("../fonts/montserrat-bold-webfont.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'montserrat';
  src: url("../fonts/montserrat-regular-webfont.woff2") format("woff2"), url("../fonts/montserrat-regular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

* {
  box-sizing: border-box;
  margin: 0;
}

.body {
  font-family: 'montserrat', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 400;
}

.container {
  max-width: 647px;
  min-width: 255px;
  margin: 0 30px;
}

.container--wide {
  min-width: 285px;
  margin: 0 auto;
}

.container--flex-column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.wrapper {
  background-image: url(../img/background-img.svg), linear-gradient(228.57deg, #323264 11.93%, #323264 57.17%, #643E72 87.46%);
  background-repeat: no-repeat;
  background-position: top center;
}

.header {
  display: grid;
  grid-template-rows: auto auto;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  margin: 0 15px;
}

.header__languages {
  list-style: none;
  display: flex;
  align-items: center;
  padding-left: 15px;
  padding-top: 10px;
  padding-bottom: 14px;
  background-color: rgba(245, 245, 250, 0.2);
  margin-left: -15px;
}

.header__languages-list-item {
  text-decoration: none;
  padding: 5px 7px;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  letter-spacing: 1px;
  color: white;
  text-transform: uppercase;
  border-radius: 8px;
}

.header__languages-list-item:hover:not(.header__languages-list-item--activated) {
  background-color: rgba(255, 255, 255, 0.2);
}

.header__languages-list-item--activated {
  background-color: #F0145A;
}

.header__address {
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  color: #B3B3BA;
  display: none;
  align-items: center;
}

.header__address::before {
  content: '';
  width: 6px;
  height: 6px;
  background-color: #E6E6F0;
  display: inline-flex;
  border-radius: 50%;
  margin-right: 6px;
}

.header__address::after {
  content: '';
  width: 6px;
  height: 6px;
  background-color: #E6E6F0;
  display: inline-flex;
  border-radius: 50%;
  margin-left: 6px;
}

.header__phone {
  background-color: rgba(245, 245, 250, 0.2);
  padding-right: 15px;
  padding-top: 10px;
  padding-bottom: 15px;
  margin-right: -15px;
  display: flex;
  justify-content: flex-end;
}

.header__phone-item {
  text-decoration: none;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  color: white;
}

.header__facebook {
  display: none;
}

.header__nav {
  display: none;
}

.header__page {
  display: none;
}

.header__logo {
  justify-self: flex-start;
  margin: 15px 0 10px;
}

.header__burger {
  justify-self: flex-end;
  font-size: 0;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  border-radius: 8px;
}

.header__burger:active {
  background-color: #E6E6F0;
}

.header__burger:active svg path {
  stroke: #323264;
}

.solution {
  padding-top: 129px;
}

.solution__box {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.solution__title {
  font-weight: 700;
  font-size: 38px;
  line-height: 46px;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 177px;
}

.solution__arrow {
  display: inline-flex;
  width: 42px;
  height: 42px;
  opacity: 0.5;
  border: 2px solid #FFFFFF;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-bottom: 75px;
}

.solution__arrow:hover svg path {
  stroke: #F0145A;
}

.solution__arrow:hover {
  border-color: #F0145A;
  opacity: 1;
}

.solution__arrow svg {
  margin-top: 3px;
}

.solution__scroll {
  display: flex;
  overflow: auto;
  align-self: flex-start;
  -ms-overflow-style: none;
  scrollbar-width: none;
  margin-bottom: 30px;
  margin-right: -30px;
}

.solution__scroll::-webkit-scrollbar {
  display: none;
}

.solution__scroll-button {
  white-space: nowrap;
  font-weight: 700;
  font-size: 30px;
  line-height: 37px;
  color: #B3B3BA;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  margin-right: 30px;
}

.solution__scroll-button--activated {
  background: linear-gradient(90.13deg, #F0145A 0.84%, #EF8080 99.96%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.solution__scroll-button:last-of-type {
  margin-right: 0;
}

.solution__text {
  border-top-width: 4px;
  border-top-style: solid;
  border-top-color: rgba(255, 255, 255, 0.3);
  padding-top: 30px;
  display: none;
}

.solution__text--activated {
  display: block;
}

.solution__text h3 {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: white;
}

.solution__text p {
  font-size: 19px;
  line-height: 30px;
  color: white;
  margin-bottom: 30px;
}

.services__heading {
  margin-top: 10px;
  font-weight: 700;
  font-size: 38px;
  line-height: 46px;
  background: linear-gradient(90.13deg, #F0145A 0.84%, #EF8080 99.96%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.services__offers {
  display: flex;
  flex-direction: column;
}

.services__offers-item {
  margin-top: 30px;
}

.services__offers-item:last-of-type {
  margin-bottom: 60px;
}

.services__offers-item-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 92px;
  height: 92px;
  background-color: white;
  border-radius: 24px;
  margin-bottom: 30px;
}

.services__offers-item-heading {
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  color: #24A3FF;
  margin-bottom: 15px;
}

.services__offers-item-paragraph {
  font-size: 16px;
  line-height: 26px;
  color: white;
}

.team {
  position: relative;
  padding-top: 50px;
}

.team::after {
  content: '';
  display: block;
  background: #CACADB;
  border-radius: 0px 0px 12px 12px;
  height: 10px;
  position: absolute;
  top: 0;
  bottom: 50%;
  left: 20px;
  right: 20px;
}

.team::before {
  content: '';
  display: block;
  background: #E7E7F2;
  border-radius: 0px 0px 12px 12px;
  height: 10px;
  position: absolute;
  left: 40px;
  right: 40px;
  top: 10px;
  bottom: 0;
}

.team__card {
  display: none;
}

.team__card--activated {
  display: block;
}

.team__card-pic {
  height: 500px;
  width: 100%;
  background-image: url(../../img/team-face.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 12px 12px 0px 0px;
}

.team__card-text {
  padding-top: 30px;
  background-color: #EDEDF7;
  border-radius: 0px 0px 12px 12px;
  padding-bottom: 30px;
}

.team__card-box {
  margin: 0 30px;
}

.team__card-box-quote {
  background: linear-gradient(90.13deg, #F0145A 0.84%, #EF8080 99.96%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
  font-size: 30px;
  line-height: 37px;
  margin-bottom: 30px;
}

.team__card-box-name {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #B3B3BA;
}

.team__card-box-job {
  font-size: 14px;
  line-height: 22px;
  color: #B3B3BA;
}

.team__scroll {
  margin-top: 30px;
}

.team__scroll-button {
  background-color: #24A3FF;
  border: none;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  opacity: 0.2;
  margin-right: 15px;
  cursor: pointer;
}

.team__scroll-button--activated {
  opacity: 1;
}

.team__scroll-button:last-of-type {
  margin-right: 0;
}

.page-link {
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-decoration: none;
  color: #24A3FF;
}
